.container-fluid{
    padding-top:30px;
    padding-left:10%;
    padding-right:10%;
    width:100vw;
    
}

div.row {
    clear: both;
}
.btn-secondary {
    background-color: #FFD7A2;
    border-color:#FFD7A2;
    border-style: solid;
    border-width: 1px;
    margin: 5px;
}
.btn-secondary:focus{
    background-color:#EB6864;
    border-color:#EB6864;
    color: #FFD7A2;
    border-style: solid;
    border-width: 1px;
}

.btn-secondary:hover{
    background-color:#EB6864;
    border-color:#EB6864;
    color: #FFD7A2;
    border-style: solid;
    border-width: 1px;
}

.dropdown-item:hover, .dropdown-item:focus {
    color: #151515;
    text-decoration: none;
    background-color: #EB6864;
    z-index:10000000000;
}
.dropdown-menu{
    z-index:5000;
}
.card-header{
    background-color: #FFD7A2;
    color:white;
    text-align:center;
    font-size:120%;
    font-weight:bold;
    height: 60px;
    padding:auto 0;
}
.card-bottom{
    height:50px;
    text-align:center;
    background-color:#FFBDA8;
}
.card{
    margin:10px 10px;
    z-index:0;
    -webkit-transition: .1s;
    transition: .1s;
}

ul{
    padding:0;
    margin-bottom:-2px;
}
.list-group-item{
    padding:0;
    text-align:center;
}
img{
    width:200px;
    height:200px;
    overflow: hidden;
    background-size: cover;
    background-position: center center;
}
.grid{
    height:90vh;
}
.container-fluid{
    height:90vh;
}
.notFound{
    display:block;
    width: 100%;
}
.btn-secondary {
