@media screen and (min-width:1080px) {
body{
    overflow-y: hidden;
}
.big-card{
    margin:auto;
    max-width: 40vw;
    height:70vh;
    border-radius: 39px;
    
    
}
.review-container{
    height:50vh;
    overflow-y: scroll;
}
.big-picture{
    height:40vh;
    width:40vh;
    max-width: 100vw;
}
.big-header:first-child{
    border-radius: 39px 39px 0 0;
    height:100px;
    font-size:200%;
}
.big-bottom{
    height:100px;
    text-align:center;
    background-color:#FFBDA8;
    position:relative;
    bottom:0;
    border-radius: 0 0 39px 39px;
    display: block;
    /* vertical-align: middle; */
    /* width:100%; */
}
.close-card{
    float:right;
}
.card_blocks{
    width:33.333%;
    float:left;
    /* height:100%; */
}
.reviews{
    vertical-align: middle;
    border-radius:0 0 0 39px;
    border-right:solid black 1px;
}
.delivery{
    
    border-right:solid black 1px;
    border-left:solid black 1px;
}
.rides{

    border-radius:0 0 39px 0;
    border-left:solid black 1px;
    
}

}

